.container {
    display: flex;
    flex-direction: column;
    padding: 32px 64px 0 128px;
    min-height: 100vh; }

@media ( max-width: 1439px) {
    .container {
        padding: 32px 32px 0 96px; } }

@media (max-width: 767px) {
    .container {
        padding: 0;
        padding-top: 16px; } }
